import React from 'react';
export function getChildrenOfType(children, type) {
    return React.Children.toArray(children).filter(function (x) { return x['type'] === type; });
}
export function getOnlyChildOfType(children, type) {
    return getChildrenOfType(children, type);
}
export function getElevationClass(elevation) {
    if (elevation > 0)
        return "md-elevation-" + elevation;
    return '';
}
